import { ErrorHandler, NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { BackandService } from '@backand/angular2-sdk';
import { AppRate } from '@ionic-native/app-rate';
import { AppVersion } from '@ionic-native/app-version';
import { Camera } from '@ionic-native/camera';
import { Deeplinks } from '@ionic-native/deeplinks';
import { FCM } from '@ionic-native/fcm';
import { File } from '@ionic-native/file';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { SocialSharing } from '@ionic-native/social-sharing';
import { Transfer } from '@ionic-native/transfer';
import { IonicStorageModule } from '@ionic/storage';
import { InlineSVGModule } from 'ng-inline-svg';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import io from 'socket.io-client';
window["io"] = io;

import { MyApp } from './app.component';
import { NavModule } from '../pages/shared/nav';
import { BackandAuthService, BackandItemService } from '../providers/backand';
import { FormHandler, PictureService, UserService } from '../providers/myservices';
import { EbcSvgComponentModule } from '../components/ebc-svg/ebc-svg.module';
import { EbcEmailComponentModule } from '../components/ebc-email/ebc-email.module';
import { EbcSmsComponentModule } from '../components/ebc-sms/ebc-sms.module';

const config = {
  scrollAssist: false,
  tabsHighlight: true,
  tabsHideOnSubPages: true
}

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    HttpModule,
    EbcEmailComponentModule,
    EbcSmsComponentModule,
    EbcSvgComponentModule,
    InlineSVGModule.forRoot({ baseUrl: 'https://cors-anywhere.herokuapp.com/' }),
    IonicModule.forRoot(MyApp, config, 
{
  links: [
    { loadChildren: '../pages/create/create.page.module#CreatePageModule', name: 'create-account', segment: 'create-account', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/shared/sidemenu/sidemenu.module#SideMenuModule', name: 'menu', segment: 'menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.page.module#LoginPageModule', name: 'login', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/myperson/myperson.page.module#PersonPageModule', name: 'profile', segment: null, priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/editperson/editperson.page.module#EditPageModule', name: 'edit', segment: null, priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/submit/submit.page.module#SubmitModule', name: 'submit', segment: 'submit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/submit/submit-confirm.modal.module#SubmitConfirmModule', name: 'SubmitConfirm', segment: 'submit-confirm.modal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/about-help/about-help.module#AboutHelpPageModule', name: 'about-help', segment: 'about-help', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/deep-item/deep-item.module#DeepItemPageModule', name: 'deep', segment: 'deep/:id', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/detail/detail.page.module#DetailPageModule', name: 'detail', segment: 'item', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/mystuff.tabs.module#MyStuffModule', name: 'my-stuff', segment: 'm1', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/samples.tabs.module#SamplesModule', name: 'samples', segment: 's0', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/templates.tabs.module#TemplatesModule', name: 'templates', segment: 't3', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/share-modal/share-modal.module#ShareModalComponentModule', name: 'share', segment: 'share-modal', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/list/mycards.page.module#MyCardsPageModule', name: 'my-cards', segment: 'mc', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/list/myflys.page.module#MyFlysPageModule', name: 'my-flyers', segment: 'mf', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/list/scards.page.module#SCardsPageModule', name: 'sample-cards', segment: 'sc', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/list/sflyers.page.module#SFlysPageModule', name: 'sample-flyers', segment: 'sf', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/list/tcards.page.module#TCardsPageModule', name: 'template-cards', segment: 'tc', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/items/list/tflyers.page.module#TFlysPageModule', name: 'template-flyers', segment: 'tf', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/private-policy/private-policy.module#PrivatePolicyPageModule', name: 'private-policy', segment: 'private-policy', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settings/settings.module#SettingsPageModule', name: 'settings', segment: 'settings', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot(),
    NavModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    AppRate,
    AppVersion,
    Camera,
    Deeplinks,
    FCM,
    File,
    BackandAuthService,
    BackandItemService,
    BackandService,
    FormHandler,
    PictureService,
    StatusBar,
    SplashScreen,
    SocialSharing,
    Transfer,
    UserService
  ]
})
export class AppModule { }



